@media md {
  .material-icons.md\:hidden {
    @apply hidden; } }

[data-topMenuBurger="active"] {
  @apply text-primary; }

[data-hasOrder="show"] {
  @apply flex; }

[data-hasOrder="hidden"] {
  @apply hidden; }

[data-popUp="show"] {
  @apply flex; }

[data-popUp="hidden"] {
  @apply hidden; }

[data-loading="show"] {
  @apply flex; }

[data-loading="hidden"] {
  @apply hidden; }
